<template>
  <div class="visitorArea">
    <div class="top" :class="{small:small}">
      <p class="lf"><span>地区</span><i></i><span>人数</span></p>
      <p class="rt">
        <button :class="active===0?'active':''" type="button" @click="tabToggle(0)">外地访客</button>
        <button :class="active===1?'active':''" type="button" @click="tabToggle(1)">当地访客</button>
      </p>
    </div>
    <div class="echart" ref="echart"></div>
    <div class="border"></div>
  </div>
</template>

<script>
export default {
  name: 'visitorArea',
  props: {
    isLoading: {
      type: Boolean,
      default: true
    },
    small:{
      type: Boolean,
      default: false
    },
    activeIdx: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      active: 0,
      myChart: null,
      chartData: {},
      chartData0: {
        seriesName: '外地访客',
        xAxisData: ['北京', '上海', '广州', '深圳', '天津', '重庆'],
        seriesData: [310, 52, 200, 334, 390, 330]
      },
      chartData1: {
        seriesName: '本地访客',
        xAxisData: ['郑州', '开封', '洛阳', '焦作', '商丘', '新乡'],
        seriesData: [800, 500, 350, 700, 600, 330]
      },
    }
  },
  watch: {
    seriesData: {
      handler(newValue, oldValue) {
        this.echart();
      },
      deep: true
    },
    isLoading(newValue, oldValue) {
      if (newValue)
        this.myChart.showLoading({text: "正在加载中..."});
      else this.myChart.hideLoading();
    }
  },
  mounted() {
    this.chartData = this.chartData0;
    this.myChart = this.$echart.init(this.$refs.echart);
    this.echartFun();
  },
  methods:{
    init() {

    },
    tabToggle(val) {
      if(this.active !== val){
        this.active = val;
        this.chartData  = this['chartData'+val]
        this.echartFun();
      }
    },
    echartFun() {
      let that = this;
      that.myChart.showLoading({ text: "正在加载中..." });
      let option = {
        color: ['#810A0C'],
        tooltip : {
            trigger: 'axis',
            formatter: "{a} <br/>{b} : {c}人",
            axisPointer : {
                type : 'shadow'
            }
        },
        grid: {
            top: -5,
            left: '1%',
            right: '2%',
            bottom: that.small?0:5,
            containLabel: true
        },
        xAxis : [
            {
              type : 'category',
              data : that.chartData.xAxisData,
              position: 'top',
              axisLabel: {
                color: '#fff',
                fontSize: that.small?6:8,
                interval: 0,
                formatter: "{value}\n{i|}",
                margin: that.small?2:10,
                rich: {
                  i: {
                    width: 4,
                    height: 4,
                    borderRadius: 2,
                    backgroundColor: '#810A0C'
                  },
                }
              },
              axisLine: {
                show: false,
              },
              axisTick: {
                show: true,
                alignWithLabel: true,
                length: that.small?43:62,
                lineStyle: {
                  color: '#666',
                  opacity: 0.7
                }
              },
              splitLine: {
                show: false
              }
            }
        ],
        yAxis : [
            {
              type : 'value',
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                color: '#fff',
                fontSize: that.small?6:8,
              },
              splitLine: {
                show: false
              }
            }
        ],
        series : [
            {
                name: that.chartData.seriesName,
                type: 'bar',
                barWidth: that.small?4:6,
                data: that.chartData.seriesData
            }
        ]
      };
      that.myChart.setOption(option);
      that.myChart.hideLoading();
    }
  }
}
</script>

<style scoped lang="scss">
  .visitorArea{
    width: 100%;
    height: 95%;
    position: relative;
    padding-top: 17px;
  }
  .top{
    position: absolute;
    top: -5px;
    left: 0;
    right: 0;
    line-height: 26px;
    .lf{
      float: left;
      span{
        display: inline-block;
        font-size: 12px;
        -webkit-transform: scale(0.8);
        transform: scale(0.8);
      }
      i{
        display: inline-block;
        width: 1px;
        height: 9px;
        background: #ddd;
        margin: 0 2px;
      }
    }
    .rt{
      float: right;
      button{
        color: #fff;
        background: transparent;
        border: 1px solid #9B433D;
        font-size: 12px;
        padding: 3px 7px 5px;
        border-radius: 15px;
        -webkit-transform: scale(0.7);
        transform: scale(0.7);
        margin-right: -10px;
        &.active{
          border-color: #80090A;
        }
      }
    }
    &.small{
      transform: scale(.7);
      left: -20%;
      right: -20%;
    }
  }
  .echart{
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 2;
  }
  .border{
    position: absolute;
    z-index: 1;
    top: 33px;
    left: 1%;
    right: 2%;
    bottom: -5px;
    border: 1px dashed #666;
  }
</style>
