      tabActive3: 0,
      energyGroup: [
        {
          top: '10',
          left: '20',
          busid: '',
          relay: '继电器',
          value: '10',
          device: [
            {
              AMP: 306,
              KWH: 112633,
              KWH_HISTORY: 112566,
