        {
          top: '21',
          left: '50',
          busid: '',
          relay: '继电器',
          value: '50',
          device: [
            {
              AMP: 306,
              KWH: 112633,
              KWH_HISTORY: 112566,
              KWH_TODAY: 0,
              SWITCH: 7266,
              SWITCH_HISTORY: 7191,
              SWITCH_TODAY: 0,
              devId: "1541529845",
              nm: "3333 1-2"
            },
          ]
        },
      ],
      energyGroupPosition: [
        {top:10,left:20},
        {top:21,left:50},
        {top:34.5,left:36},
      ],
    }
  },
  created (){
    /* this.energyGroup.forEach((item,idx)=>{
      item.data.forEach((dataItem)=>{
        item.maxData = dataItem.value>item.maxData?dataItem.value:item.maxData;
      });
    }); */
  },
  mounted (){
    this.init();
  },
  methods: {
    init (){
      this.swiper();
      this.getEnergyList();
    },
    swiper (){
      let that = this;
      let swiper = new that.$Swiper('.swiper-container',{
        direction : 'vertical',
        followFinger : false,
        speed:500,
        mousewheel: true,
        autoplay: {
          delay: 5000,
          stopOnLastSlide: false,
          disableOnInteraction: false,
        },
        on:{
          init:function(){
            let slide=this.slides.eq(0);
            slide.addClass('ani-slide');
          },
          transitionStart: function(){
            for(let i=0;i<this.slides.length;i++){
              let slide=this.slides.eq(i);
              slide.removeClass('ani-slide');
            }
          },
          transitionEnd: function(){
            let slide=this.slides.eq(this.activeIndex);
            slide.addClass('ani-slide');
          },
        }
      });
      //swiper.slideTo(3);
      that.$refs.swiperContainer.addEventListener('mouseover',function(){
        swiper.autoplay.stop();
      });
      that.$refs.swiperContainer.addEventListener('mouseout',function(){
        swiper.autoplay.start();
      });
    },
    tabActiveFun3(idx) {
      this.tabActive3 = idx;
      console.log(this.tabActive3);
    },
    getEnergyList() { //获取继电器
      const that = this;
      this.$api.getEnergyList().then((res) => {
        if(res.code==='success'){
          console.log(res);
          if(res.data.length<=0) return;
          res.data.forEach((item,idx)=>{
            that.energyGroup.push({...that.energyGroupPosition[idx],...item});
          });
          console.log(that.energyGroup);
          let params = {
            begin: -1,
            end: -1
          }
          this.energyGroup.forEach((item,idx) => {
            this.$api.getDeviceDetailList(item.busid,params).then((res) => {
              console.log(res);
              if(res.code === 'success'){
                item.device = res.data;
              }
            });
          });
        }
      });
    },
  }
}
</script>

