      </header>
      <section class="panel">
        <div class="content"><main-box></main-box></div>
      </section>
    </div>
    <div class="lf-box">
      <ul>
        <li><panel-list :idx="0"><visitor-weather :nowTime="nowTime"></visitor-weather></panel-list></li>
        <li>
          <panel-list :idx="1">
          <div class="visitor-path">
            <img class="room" src="./static/image/path-room.png" alt="访客走势图">
            <img class="arrow" src="./static/image/path-arrow.gif" alt="访客走势图">
            <img class="visitor" src="./static/image/visitor.png" alt="访客">
          </div>
          </panel-list>
        </li>
        <li><panel-list :idx="2"><visitor-gender></visitor-gender></panel-list></li>
      </ul>
    </div>
    <div class="rt-box">
      <ul>
        <li><panel-list :idx="3"><visitor-energy></visitor-energy></panel-list></li>
        <li><panel-list :idx="4"><visitor-area></visitor-area></panel-list></li>
        <li><panel-list :idx="5"><visitor-age></visitor-age></panel-list></li>
      </ul>
    </div>
  </div>
</template>

<script>
import mainBox from './components/mainBox.vue'
import panelList from './components/panelList.vue'
//环境监测
import visitorWeather from './components/panelComponents/visitorWeather.vue'
//访客性别
import visitorGender from './components/panelComponents/visitorGender.vue'
//能耗监测
import visitorEnergy from './components/panelComponents/visitorEnergy.vue'
//访客区域
import visitorArea from './components/panelComponents/visitorArea.vue'
//访客年龄
import visitorAge from './components/panelComponents/visitorAge.vue'

export default {
  name: 'app',
  components: {
    mainBox,
    panelList,
    visitorWeather,
    visitorEnergy,
    visitorGender,
    visitorArea,
    visitorAge
  },
  data() {
    return {
      nowTime: new Date(),
    }
  },
  mounted() {
    this.countDown();
  },
  methods:{
    countDown() {
      var that = this;
      that.nowTime = new Date();
      setInterval(()=>{
        that.nowTime = new Date(that.nowTime).getTime() + 1000;
      },1000);
    }
  }
}
</script>

