<template>
  <!-- <model-loading v-bind="loading"></model-loading> -->
    <div class="visitorAge" ref="echart"></div>
</template>

<script>
export default {
  name: 'visitorAge',
  props: {
    isLoading: {
      type: Boolean,
      default: true
    },
    small:{
      type: Boolean,
      default: false
    },
    activeIdx: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      myChart: null,
      loading: {
        code: 10000,
        message: ''
      }
    }
  },
  watch: {
    seriesData: {
      handler(newValue, oldValue) {
        this.echart();
      },
      deep: true
    },
    loading(newValue, oldValue) {
      if (newValue){
        if(newValue.code === 10000){
          this.myChart = this.$echart.init(this.$refs.visitorAge);
          this.echartFun();
        }
      }
    }
  },
  mounted() {
    this.myChart = this.$echart.init(this.$refs.echart);
    this.echartFun();
  },
  methods:{
    echartFun() {
      let that = this;
      //that.myChart.showLoading({ text: "正在加载中..." });
      let option = {
        tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
            orient: 'vertical',
            top: '15%',
            right: that.small?0:'5%',
            align: 'left',
            backgroundColor: '#113048',
            borderColor: '#fff',
            borderWidth: 1,
            padding: that.small?[2,-18,2,5]:[5,5,5,12],
            itemWidth: that.small?6:10,
            itemHeight: that.small?6:10,
            itemGap: that.small?2:3,
            textStyle: {
              color: '#fff',
              fontSize: that.small?6:10,
            },
            data: ['20岁以下','21-40岁','41-60岁','61岁以上']
        },
        series : [
            {
                name:'访客年龄比例',
                type:'pie',
                radius : '80%',
                center: ['30%', '50%'],
                data:[
                    {value:260, name:'20岁以下',itemStyle:{color:'#FFFFFF'}},
                    {value:310, name:'21-40岁',itemStyle:{color:'#E2AF38'}},
                    {value:274, name:'41-60岁',itemStyle:{color:'#1E84AC'}},
                    {value:235, name:'61岁以上',itemStyle:{color:'#AD1100'}}
                ].sort(function (a, b) { return a.value - b.value; }),
                roseType: 'radius',
                label: {
                  textStyle: {
                    color: '#fff'
                  },
                  fontSize: that.small?6:8,
                },
                labelLine: {
                    normal: {
                        lineStyle: {
                            color: 'rgba(255, 255, 255, 0.3)'
                        },
                        smooth: 0,
                        length: that.small?2:5,
                        length2: that.small?5:20
                    }
                },
                animationType: 'scale',
                animationEasing: 'elasticOut',
                animationDelay: function (idx) {
                    return Math.random() * 200;
                }
            }
        ]
      };
      that.myChart.setOption(option);
    }
  }
}
</script>

<style scoped lang="scss">
  .visitorAge{
    width: 100%;
    height: 100%;
  }
</style>
