import axios from 'axios'

let mineAxios = axios.create({ 
  timeout: 200000,
  withCredentials: true,
  baseURL: 'http://47.110.53.42:8080/v1/control/WYIC102639D4EACB2739',
  headers: {
    'X-Requested-With': 'XMLHttpRequest',
    post: {
      "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8"
    }
  }
});
export function get(url, config) {
  return new Promise((resolve, reject) => {
    mineAxios.get(url, {params:config}).then(
        response => {
          resolve(response.data);
        },
        err => {
          reject(err);
        }
      ).catch(error => {
        reject(error);
      });
  });
}
export function post(url, params) {
  return new Promise((resolve, reject) => {
    mineAxios.post(url, params).then(
        response => {
          resolve(response.data);
        },
        err => {
          reject(err);
        }
      ).catch(error => {
        reject(error);
      });
  });
}
export default {
  /* 获取天气数据 */
  heweather(data) {
    return new Promise((resolve, reject) => {
      axios.get('https://free-api.heweather.com/s6/weather/forecast', {params:data}).then(
          response => {
            resolve(response.data);
          },
          err => {
            reject(err);
          }
        ).catch(error => {
          reject(error);
        });
    });
  },
  /* 馆内能耗监测 */
  //继电器
  getEnergyList(data) {
    return get('/relay',data);
  },
  //继电器设备列表
  getDeviceDetailList(id,data) {
    return get(`/relay/${id}/DeviceDetailList`,data);
  },
}