<template>
  <div class="visitorEnergy">
    <div class="lf-box">
      <div class="echart" ref="echart"></div>
        <div class="energy-bg">
          <div class="energyTime">
            <p class="text">馆&nbsp;内</p>
            <p class="time">用电时长{{energyTime}}h</p>
          </div>
          <p class="energyUsed"><span>{{energyUsed}}</span>W</p>
          <div class="energyDay">
            <p>日均耗电</p>
            <span>{{energyDay}}度</span>
            <ul>
              <li v-for="(item,idx) in energyList" :key="idx"><p :style="{width:item+'%'}"></p></li>
            </ul>
          </div>
        </div>
    </div>
    <div class="rt-box">
      <div class="echart" ref="echartBar"></div>
      <div class="text-box">
        <p v-for="(item,idx) in energyData" :key="idx">{{item.label}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'visitorEnergy',
  props: {
    isLoading: {
      type: Boolean,
      default: true
    },
    activeIdx: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      myChart: null,
      energyUsed: 195,
      energyTime: 46,
      energyDay: 0.84,
      energyList: [45,50,55,60],
      energyData: [
        {label:'用电时长',max:'100',value:'40'},
        {label:'用电量',max:'100',value:'50'},
        {label:'用电费用',max:'100',value:'60'}
      ],
    }
  },
  watch: {
    seriesData: {
      handler(newValue, oldValue) {
        this.echart();
      },
      deep: true
    },
    isLoading(newValue, oldValue) {
      if (newValue)
        this.myChart.showLoading({text: "正在加载中..."});
      else this.myChart.hideLoading();
    },
    activeIdx(newValue, oldValue) {
      console.log(newValue)
    }
  },
  mounted() {
    this.myChart = this.$echart.init(this.$refs.echart);
    this.myChartBar = this.$echart.init(this.$refs.echartBar);
    this.init();
  },
  methods:{
    init() {
      this.echartFun();
      this.echartBarFun();
    },
    echartFun() {
      let that = this;
      that.myChart.showLoading({ text: "正在加载中..." });

      let option = {
        angleAxis: {
          clockwise: false,
          type: 'category',
          data: new Array(20),
          axisLine: {
            show: false
          },
          axisLabel: {
            show: false
          },
          axisTick: {
            show: false
          },
          z: 10
        },
        radiusAxis: {
          splitNumber: 1,
          min: 0,
          max: 14,
          axisLabel: {
            show: false
          },
          axisLine: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisTick: {
            show: false
          },
        },
        polar: {
          center: ['27%','50%'],
          radius: '85%',
        },
        series: [
          {
            type: 'bar',
            data: new Array(20).fill(12),
            coordinateSystem: 'polar',
            stack: 'a',
            z: 4,
            barCategoryGap: '40%',
            itemStyle: {
              normal: {
                  color: 'rgba(0,0,0,0)'
              },
              emphasis: {
                  color: 'rgba(0,0,0,0)'
              }
            },
          },
          {
            type: 'bar',
            data: new Array(20).fill(1,0,15).fill(0,16,19),
            coordinateSystem: 'polar',
            stack: 'a',
            z: 3,
            itemStyle: {
              normal: {
                  color: '#009CFF'
              },
              emphasis: {
                  color: '#009CFF'
              }
            },
          },
          {
            type: 'bar',
            data: new Array(20).fill(12),
            coordinateSystem: 'polar',
            stack: 'b',
            z: 2,
            barGap: '-100%',
            itemStyle: {
              normal: {
                  color: 'rgba(0,0,0,0)'
              },
              emphasis: {
                  color: 'rgba(0,0,0,0)'
              }
            },
          },
          {
            type: 'bar',
            data: new Array(20).fill(1),
            coordinateSystem: 'polar',
            stack: 'b',
            z: 1,
            itemStyle: {
              normal: {
                  color: '#C2C0C0'
              },
              emphasis: {
                  color: '#C2C0C0'
              }
            },
          }
        ]
      };
      that.myChart.setOption(option);
      that.myChart.hideLoading();
    },
    echartBarFun() {
      let that = this;
      that.myChartBar.showLoading({ text: "正在加载中..." });
      let sunData=[], itemData=[];
      that.energyData.forEach((item,idx)=>{
        itemData.push(item.value)
        sunData.push({name:item.label,value:item.value});
      });
      let option = {
          title: {
            show: false
          },
          grid: {
            left: "5%",
            top: 70,
            bottom: 0
          },
          tooltip: {
            "trigger": "item",
            "textStyle": {
              "fontSize": 12
            },
            "formatter": "{b0} : {c0}"
          },
          xAxis: {
            "max": 100,
            "splitLine": {
              "show": false
            },
            "axisLine": {
              "show": false
            },
            "axisLabel": {
              "show": false
            },
            "axisTick": {
              "show": false
            }
          },
          yAxis: [
            {
              "type": "category",
              "data": [
                {
                  value:'用电时长',
                },
                {
                  value:'用电量',
                },
                {
                  value:'用电费用',
                }
              ],
              inverse: true,
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                show: false
              }
            },
          
          ],
          series: [
            {
              "type": "pictorialBar",
              "itemStyle": {
                color: '#176D9E',
              },
              symbol: 'rect',
              "symbolSize": [2,8],
              "animationDuration": 0,
              barCategoryGap: '0',
              "symbolRepeat": true,
              "symbolMargin": "2",
              "symbolBoundingData": 100,
              "symbolPosition": "start",
              "symbolOffset": [ 20, 0 ],
              "data": itemData,
              "z": 10
            },
            {
              "type": "pictorialBar",
              "itemStyle": {
                color: '#ffffff',
              },
              symbol: 'rect',
              "symbolSize": [2,8],
              "animationDuration": 0,
              barCategoryGap: '0',
              "symbolRepeat": "fixed",
              "symbolMargin": "2",
              "symbolBoundingData": 100,
              "symbolPosition": "start",
              "symbolOffset": [ 20, 0 ],
              "data": sunData,
              "z": 5,
            }
          ]
        };
      that.myChartBar.setOption(option);
      that.myChartBar.hideLoading();
    }
  }
}
</script>

