<template>
  <div class="visitorPath">
    <div class="echart" ref="echart"></div>
    <p class="text">人数走势变化</p>
  </div>
</template>

<script>
export default {
  name: 'visitorPath',
  props: {
    isLoading: {
      type: Boolean,
      default: true
    },
    activeIdx: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      myChart: null,
    }
  },
  watch: {
    seriesData: {
      handler(newValue, oldValue) {
        this.echart();
      },
      deep: true
    },
    isLoading(newValue, oldValue) {
      if (newValue)
        this.myChart.showLoading({text: "正在加载中..."});
      else this.myChart.hideLoading();
    },
    activeIdx(newValue, oldValue) {
      console.log(newValue)
    }
  },
  mounted() {
    this.myChart = this.$echart.init(this.$refs.echart);
    this.echartFun();
  },
  methods:{
    init() {

    },
    echartFun() {
      let that = this;
      that.myChart.showLoading({ text: "正在加载中..." });
      let option = {
        title: {
          show: false
        },
        grid: {
          left: 40,
          right: 10,
          top: 10,
          bottom: 15
        },
        xAxis: {
            type: 'category',
            boundaryGap: false,
            axisTick: {
              show: false
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#999',
                type: 'dotted'
              }
            },
            axisLabel: {
              color: '#fff',
              interval: 0,
              fontSize: 7,
              margin: 2,
              showMaxLabel: true
            },
            data: ['8:00', '10:00', '12:00', '14:00', '16:00', '18:00']
        },
        yAxis: {
            type: 'value',
            axisTick: {
              show: false
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#999',
                type: 'dotted'
              }
            },
            axisLabel: {
              color: '#fff',
              fontSize: 7,
              margin: 2,
            },
            splitLine: {
              show: false
            }
        },
        series: [{
            data: [820, 332, 901, 334, 1590, 330],
            type: 'line',
            smooth:true,
            sampling: 'average',
            symbol: 'none',
            itemStyle: {
              color: '#236177',
              borderWidth: .5
            },
            areaStyle: {
              color: '#236177',
              opacity: .5
            }
        }]
      };
      that.myChart.setOption(option);
      that.myChart.hideLoading();
    }
  }
}
</script>

