import Vue from 'vue'
import App from './App.vue'
import Swiper from 'swiper'
import echart from 'echarts'
import axios from 'axios'
import api from './static/js/api'
import modelLoading from '@/components/modelLoading'

import 'swiper/dist/css/swiper.css';
require("@/static/css/reset.scss");

//全局变量
Vue.prototype.$Swiper = Swiper;
Vue.prototype.$echart = echart;
Vue.prototype.$axios = axios;
Vue.prototype.$api = api;
Vue.prototype.systemList = ['馆内环境监测','馆内访客走势','访客性别比例','馆内能耗监测','访客来自区域','访客年龄比例'];

//全局组件
Vue.component("model-loading",modelLoading);

Vue.config.productionTip = false;

//过滤器

Vue.filter("dateFormat", function (value) { //时间 yyyy-MM-dd HH:mm:ss
  if (!value) return null;
  value = new Date(value);
  return value.getFullYear() + (value.getMonth() + 1 > 9 ? "-" : "-0") + (value.getMonth() + 1) + (value.getDate() > 9 ? "-" : "-0") + value.getDate() + " " + (value.getHours() > 9 ? "" : "0") + value.getHours() + (value.getMinutes() > 9 ? ":" : ":0") + value.getMinutes() + (value.getSeconds() > 9 ? ":" : ":0") + value.getSeconds();
});
Vue.filter("dateYear", function (value) { //时间 yyyy
  if (!value) return null;
  value = new Date(value);
  return value.getFullYear();
});
Vue.filter("dateMonth", function (value) { //时间 MM
  if (!value) return null;
  value = new Date(value);
  return (value.getMonth() + 1 > 9 ? "" : "0") + (value.getMonth() + 1);
});
Vue.filter("dateDate", function (value) { //时间 dd
  if (!value) return null;
  value = new Date(value);
  return (value.getDate() > 9 ? "" : "0") + value.getDate();
});
Vue.filter("dateHMS", function (value) { //时间 hh:mm:ss
  if (!value) return null;
  value = new Date(value);
  return (value.getHours() > 9 ? "" : "0") + value.getHours() + (value.getMinutes() > 9 ? ":" : ":0") + value.getMinutes() + (value.getSeconds() > 9 ? ":" : ":0") + value.getSeconds();
});
Vue.filter("dateAP", function (value) { //时间 hh:mm AM/PM
  if (!value) return null;
  value = new Date(value);
  let hours = value.getHours();
  hours = hours>12?hours-12:hours;
  return (hours > 9 ? "" : "0") + hours + (value.getMinutes() > 9 ? ":" : ":0") + value.getMinutes() + (value.getHours()>12?' PM':' AM');
});
Vue.filter("dateMonthDate", function (value) { //时间 英文月 dd
  if (!value) return null;
  value = new Date(value);
  let Months = new Array('January','February','March','April','May','June','July','August','September','October','November','December');
  return Months[value.getMonth()].slice(0,3) + '. ' + (value.getDate() > 9 ? "" : "0") + value.getDate();
});
Vue.filter('week', function (value) { // 时间转星期
  if (!value) return ''
  let date = new Date(value).getDay();
  let weeks = new Array("Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday");
  return weeks[date];
})
Vue.filter('slice3', function (value) { // 截取字符串前三位
  if (!value) return ''
  return value.slice(0,3);
})

new Vue({
  render: h => h(App),
}).$mount('#app')
