<template>
  <div class="flex visitorWeather" :class="{small:small}">
    <div class="lf-box">
      <div class="top-box flex">
        <span class="week">{{nowTime | week}}</span>
        <span>{{nowTime | dateMonthDate}}</span>
        <span v-if="!small">{{nowTime | dateAP}}</span>
      </div>
      <div class="bottom-box flex">
        <div class="location" v-if="!small">
          <p>{{location}}</p>
          <img :src="`${baseUrl}weather/${today.cond_code_d}.png`" alt="天气">
        </div>
        <div class="room">
          <p class="name" v-if="small">郑州</p>
          <p class="temp">{{roomNow.tmp}}&#176;</p>
          <p class="feel">Feels like <span>{{roomNow.feel_tmp}}&#8451;</span></p>
        </div>
        <div class="out">
          <ul>
            <li><i class="icon top"></i>{{today.tmp_max}}&#8451;<i class="icon bottom"></i>{{today.tmp_min}}&#8451;</li>
            <li><i class="icon hum"></i>{{today.hum}}%</li>
            <li><i class="icon dir"></i>{{today.wind_dir}}</li>
            <li><i class="icon spd"></i>{{today.wind_spd}} Km/h</li>
          </ul>
        </div>
      </div>
    </div>
    <div class="rt-box">
      <div class="list" v-for="(item,idx) in weatherList" :key="idx">
        <p><span>{{item.date | week | slice3}}</span></p>
        <div>
          <span class="tmp">{{item.tmp_min}}~{{item.tmp_max}}&#8451;</span>
          <p><img :src="`${baseUrl}weather/${item.cond_code_d}.png`" alt="天气"><span><i class="icon hum"></i>{{item.hum}}%</span></p>
        </div>
