<script>
export default {
  name: 'visitorWeather',
  props: {
    isLoading: {
      type: Boolean,
      default: true
    },
    activeIdx: {
      type: Number,
      default: 0
    },
    small:{
      type: Boolean,
      default: false
    },
    nowTime: {
      type: [Number,Date,String],
      default() {
        return new Date();
      }
    }
  },
  data() {
    return {
      loading: {
        code: 10000,
        message: ''
      },
      baseUrl: process.env.BASE_URL,
      location: '郑州',
      roomNow: {
        tmp: 0,
        feel_tmp: 0
      },
      today: {
        tmp_max: 0,
        tmp_min: 0,
        cond_code_d: 0,
        wind_dir: '风向',
        wind_spd: '风速',
        hum: 0,
        cond_code_d: 100,
      },
      weatherList: [
        {
          date: '2018-12-08',
          tmp_max: '0',
          tmp_min: '0',
          cond_code_d: 100,
          hum: '0'
        },
        {
          date: '2018-12-08',
          tmp_max: '0',
          tmp_min: '0',
          cond_code_d: 100,
          hum: '0'
        },
        {
          date: '2018-12-08',
          tmp_max: '0',
          tmp_min: '0',
          cond_code_d: 100,
          hum: '0'
        },
        {
          date: '2018-12-08',
          tmp_max: '0',
          tmp_min: '0',
          cond_code_d: 100,
          hum: '0'
        },
      ]
    }
  },
  watch: {
    loading(newValue, oldValue) {
      
    }
  },
  created() {
    this.getWeatherData();
  },
  mounted() {
    
  },
  methods:{
    getWeatherData() {
      const that = this;
      that.$api.heweather({location:'zhengzhou',key:'0b977c152ed54a0e8af6531b5bcd835b'}).then(res=>{
        console.log(res);
        const data = res.HeWeather6[0];
        if(data.status=='ok'){
          that.location = data.basic.location;
          data.daily_forecast.forEach((item,idx)=>{
            if(idx===0){
              that.today = item;
            }else if(idx<=4){
              that.weatherList[idx-1] = item;
            }
          });
        }else{
          console.log('天气获取失败');
        }
      });
    }
  }
}
</script>

