<template>
  <div class="energyPanel">
    <div class="lf-box">
      <div class="echart" ref="echart"></div>
    </div>
    <div class="rt-box">
      <div class="echart" ref="echartBar"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'energyPanel',
  props: {
    isLoading: {
      type: Boolean,
      default: true
    },
    activeIdx: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      myChart: null,
      energyUsed: 195,
      energyTime: 46,
      energyDay: 0.84,
      energyList: [45,50,55,60],
      energyData: [
        {label:'用电时长',max:'100',value:'40'},
        {label:'用电量',max:'100',value:'50'},
        {label:'用电费用',max:'100',value:'60'}
      ],
    }
  },
  watch: {
    seriesData: {
      handler(newValue, oldValue) {
        this.echart();
      },
      deep: true
    },
    isLoading(newValue, oldValue) {
      if (newValue)
        this.myChart.showLoading({text: "正在加载中..."});
      else this.myChart.hideLoading();
    },
    activeIdx(newValue, oldValue) {
      console.log(newValue)
    }
  },
  mounted() {
    this.myChart = this.$echart.init(this.$refs.echart);
    this.myChartBar = this.$echart.init(this.$refs.echartBar);
    this.echartFun();
    this.echartBarFun();
  },
  methods:{
    init() {

    },
    echartFun() {
      let that = this;
      that.myChart.showLoading({ text: "正在加载中..." });

      let option = {
          tooltip: {
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          color: ['#FFFFFF','#A03435','#07A5FF'],
          series: [
              {
                  name:'整体占比',
                  type:'pie',
                  radius: ['30%', '60%'],
                  avoidLabelOverlap: false,
                  label: {
                    formatter: "{b}",
                    fontSize: 10
                  },
                  labelLine: {
                      normal: {
                          lineStyle: {
                              color: 'rgba(255, 255, 255, 1)'
                          },
                          smooth: 0,
                          length: 2,
                          length2: 3
                      }
                  },
                  data:[
                      {value:310, name:'A'},
                      {value:234, name:'B'},
                      {value:135, name:'C'},
                  ]
              }
          ]
      };


      that.myChart.setOption(option);
      that.myChart.hideLoading();
    },
    echartBarFun() {
      let that = this;
      that.myChartBar.showLoading({ text: "正在加载中..." });

      let option = {
        title: {
          show: false
        },
        grid: {
          left: 25,
          right: 8,
          top: 10,
          bottom: 15
        },
        xAxis: {
            type: 'category',
            boundaryGap: false,
            axisTick: {
              show: false
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#999',
                type: 'dotted'
              }
            },
            axisLabel: {
              color: '#fff',
              interval: 0,
              fontSize: 6,
              margin: 2,
              showMaxLabel: true
            },
            data: ['8:00', '10:00', '12:00', '14:00', '16:00', '18:00']
        },
        yAxis: {
            type: 'value',
            axisTick: {
              show: false
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#999',
                type: 'dotted'
              }
            },
            axisLabel: {
              color: '#fff',
              fontSize: 7,
              margin: 2,
            },
            splitLine: {
              show: false
            }
        },
        series: [{
            data: [820, 332, 901, 334, 1590, 330],
            type: 'line',
            smooth:true,
            sampling: 'average',
            symbol: 'none',
            itemStyle: {
              color: '#236177',
              borderWidth: .5
            },
            areaStyle: {
              color: '#236177',
              opacity: .5
            }
        }]
      };

      that.myChartBar.setOption(option);
      that.myChartBar.hideLoading();
    }
  }
}
</script>

