<template>
  <div :is="tag" v-if="code===-1" :class="`${position} model-loading`">
    <img src="../static/image/loading.gif" alt="加载中...">
  </div>
  <div :is="tag" v-else-if="code===10000">
    <slot></slot>
  </div>
  <div :is="tag" v-else :class="`${position} model-loading`" style="background-color:#fff;">
    <div class="layout-td">
      <h2 style="font-size:16px;margin-bottom:.6em"><span>错误({{code}})</span></h2>
      <p>
        由于{{message}}，导致页面无法显示，您可以返回上一步，或者<span class="cursor" style="color:#6896ff" @click="$emit('reloading')">点击重新加载</span>
      </p>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      position:{
        type:String,
        default:''
      },
      tag: {
        type: String,
        default: 'div'
      },
      code: {
        type: [String, Number],
        default: -1
      },
      message:{
        type: String,
        default: '服务器繁忙'
      }
    }
  }

</script>
<style>
  .model-loading {
    position: relative;
    flex-wrap: wrap;
    padding: 15px;
    min-height: 50px;
    color: #999;
    font-size: 14px;
    text-align: center;
  }
  .model-loading.middle{
    display:flex;
    align-items: center;
    justify-content: center;
  }
</style>
